@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
html,
body {
  height: 100%;
  overflow: hidden;
}

body {
  font-family: 'IBM Plex Mono', sans-serif;
  margin: 0;
  padding: 0;
  background: #26213e;
}

button {
  background-color: #7f3d69;
  color: #fff;
  padding: 0 45px;
  border: 2px solid #fff;
  border-radius: 25px;
  height: 50px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.2rem;
  cursor: pointer;
  transition: .4s;
}

button:hover {
  background-color: #d75893
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
/*
COLORS
#f3e460
#e59e7a
#d75893
#7f3d69
#26213e */

.start {
  font-size: 1.25rem;
}

button {
  color: #f3e460;
}

h1 {
  color: #f3e460;
  font-size: 5rem;
  font-family: 'Oswald', sans-serif;
  letter-spacing: 2px;
}


span {
  display: block;
}

em {
  color: #d75893;
  font-style: normal;
}

em::after {
  content: '';
  border: 1px solid white;
  color: white;
  -webkit-animation: blink .7s infinite;
          animation: blink .7s infinite;
}

@-webkit-keyframes blink {
  from {
    opacity: 100%;
  }

  to {
    opacity: 0%;
  }
}

@keyframes blink {
  from {
    opacity: 100%;
  }

  to {
    opacity: 0%;
  }
}
p {
  color: white;
}

.game h1 {
  font-size: 2rem;
}

.points span {
  font-weight: bold;
}


h3 {
  color: white;
}

.tip span {
  color: #ecfa00;
  text-transform: uppercase;
}

.wordContainer {
  margin: 1.5em;
  padding: 1.5em;
  border: 20px solid #ecfa00;
  display: flex;
}

.letter,
.blankSquare {
  font-size: 70px;
  line-height: 1.5;
  border: 3px solid #000;
  height: 100px;
  width: 100px;
  text-transform: uppercase;
  background-color: white;
  color: #000;
  font-weight: bold;
}

.letterContainer p {
  margin-bottom: 1.2em;
}

.letterContainer form {
  display: flex;
  align-items: center;
  justify-content: center;
}

.letterContainer input {
  height: 50px;
  width: 50px;
  font-size: 2em;
  text-align: center;
  margin-right: 1em;
}

.wrongLettersContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.wrongLettersContainer span {
  margin-left: 10px;
  text-transform: uppercase;
  color: #f3e460;
}
h2 {
  color: white;
}

span {
  color: #ECFa00;
  font-size: 1.5em;
}
